import pesos from "../helpers/pesosCurrency";
export default function cartItem(cartItem) {
    return "\n    <tr id=\"row" + cartItem.item.id + "\">\n      <td>\n        <div>\n          <h2>" + cartItem.item.name + "</h2>\n          </br>\n          <h3>Precio unitario: " + pesos.format(cartItem.item.getPriceWithDiscount()) + "</h3>\n        </div>\n      </td>\n      <td>\n        <button class=\"cart-button js-remove\" data-product-id=\"" + cartItem.item.id + "\"><i class=\"fas fa-trash\"></i></button>\n        </br>\n        <div class=\"cart-table-item\">\n          <button class=\"cart-button js-substract\" data-product-id=\"" + cartItem.item.id + "\">&minus;</button>\n          <p id=\"amount" + cartItem.item.id + "\">" + cartItem.amount + "</p>\n          <button class=\"cart-button js-add\" data-product-id=\"" + cartItem.item.id + "\">&plus;</button>\n        </div>\n        </br>\n        <h2 id=\"totalPrice" + cartItem.item.id + "\">\n        " + pesos.format(cartItem.amount * cartItem.item.getPriceWithDiscount()) + "\n        </h2>\n      </td>\n    </tr>\n";
}
