import Cart from "./Cart";
import Product from "./Product";
import StoreUser from "./StoreUser";
import products from "../helpers/products";
import NullProduct from "./NullProduct";
import NullStoreUser from "./NullStoreUser";
var Store = /** @class */ (function () {
    function Store() {
        this._users = [];
        this._user = new NullStoreUser();
        this._catalog = [];
        this._cart = new Cart();
        this.loadCatalog();
        this.loadUsers();
    }
    //Users
    Store.prototype.loadUsers = function () {
        var _this = this;
        var loadData = localStorage.getItem("users");
        var loadDataJson;
        if (loadData) {
            loadDataJson = JSON.parse(loadData);
            loadDataJson.forEach(function (element) {
                var user = new StoreUser();
                user.create(element);
                _this._users.push(user);
            });
        }
    };
    Object.defineProperty(Store.prototype, "user", {
        //Loged user
        get: function () {
            return this._user;
        },
        set: function (user) {
            this._user = user;
            localStorage.setItem("user", JSON.stringify(this._user));
        },
        enumerable: false,
        configurable: true
    });
    Store.prototype.registerUser = function (user) {
        var existUser = this.verifyUserExists(user.mailAdress);
        if (existUser.length == 0) {
            this._user = user;
            localStorage.setItem("user", JSON.stringify(this._user));
            this._users.push(this._user);
            localStorage.setItem("users", JSON.stringify(this._users));
        }
        else {
            throw new Error("El usuario que está intentando crear ya existe.");
        }
    };
    Store.prototype.verifyUserExists = function (userMail) {
        return this._users.filter(function (userEl) { return userEl.mailAdress == userMail; });
    };
    Store.prototype.getLogedUser = function () {
        var loadData = localStorage.getItem("user");
        if (loadData) {
            var user = new StoreUser();
            user.create(JSON.parse(loadData));
            this._user = user;
        }
        else {
            this._user = new NullStoreUser();
        }
    };
    Store.prototype.clearUser = function () {
        this._user = new NullStoreUser();
        localStorage.removeItem("user");
    };
    Store.prototype.saveUser = function () {
        localStorage.setItem("user", JSON.stringify(this._user));
        localStorage.setItem("users", JSON.stringify(this._users));
    };
    //Catalog
    Store.prototype.loadCatalog = function () {
        var _this = this;
        var loadData = localStorage.getItem("products");
        var loadDataJson;
        if (loadData) {
            loadDataJson = JSON.parse(loadData);
        }
        else {
            localStorage.setItem("products", JSON.stringify(products()));
            loadData = localStorage.getItem("products");
            loadDataJson = JSON.parse(loadData);
        }
        loadDataJson.forEach(function (element) {
            var prod = new Product();
            prod.create(element);
            _this._catalog.push(prod);
        });
    };
    Store.prototype.showCatalog = function () {
        return this._catalog;
    };
    Store.prototype.filterCatalog = function (text) {
        var filteredProducts = this._catalog.filter(function (product) {
            return product.name.toLowerCase().includes(text.trim().toLowerCase()) ||
                product.type.toLowerCase().includes(text.trim().toLowerCase());
        });
        return filteredProducts;
    };
    //Products
    Store.prototype.newProduct = function (prod) {
        this._catalog.push(prod);
        localStorage.setItem("products", JSON.stringify(this._catalog));
    };
    Store.prototype.getProductById = function (id) {
        var product = this.showCatalog().filter(function (prod) { return prod.id == id; })[0];
        id && product ? product : (product = new NullProduct());
        return product;
    };
    Store.prototype.editProduct = function () {
        localStorage.setItem("products", JSON.stringify(this._catalog));
    };
    Store.prototype.removeProduct = function (id) {
        var productIndex = this._catalog.findIndex(function (p) { return p.id == id; });
        productIndex != -1 ? this._catalog.splice(productIndex, 1) : null;
        localStorage.setItem("products", JSON.stringify(this._catalog));
    };
    Object.defineProperty(Store.prototype, "cart", {
        //Cart
        get: function () {
            return this._cart;
        },
        enumerable: false,
        configurable: true
    });
    return Store;
}());
export default Store;
